.testimonials{
    margin-top:4rem;
    display:flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 3rem;
}

.testimonials h1{
    font-size: 40px;
}

.test-container{
    display:grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap:40px;
    margin-top:2rem;
}

.test-container p{
    font-size:18px;
}

.test-card{
    width:346px;
    background-color: aqua;
    align-items: center;
    text-align: center;
    padding: 30px 20px;
    border-radius: 45px;
    background:rgba(0, 128, 0, 0.4); /* Green color with 40% opacity */;
    transition:0.4s
}

.test-card1{
    width:346px;
    background-color: aqua;
    align-items: center;
    text-align: center;
    padding: 30px 20px;
    border-radius: 45px;
    background:rgba(162, 160, 250, 0.4); /* RGBA format: (R, G, B, A) where A is opacity */
    ;
    transition:0.4s
}

.test-card2{
    width:346px;
    background-color: aqua;
    align-items: center;
    text-align: center;
    padding: 30px 20px;
    border-radius: 45px;
    background:rgba(241, 233, 124, 0.4); /* RGBA format: (R, G, B, A) where A is opacity */;
    transition:0.4s
    ;
}

.test-container button{
    border:none;
    margin:10px 20px;
    padding:10px 20px;
    border-radius: 50px;
    font-size: 15px;
}

.test-container img{
    width:100px;
    border-radius:100px;
}

.title{
    margin-top:100px
}

/* Hover Class*/

.test-card:hover{
    transition:0.4s;
    transform:scale(1.1);
    box-shadow: 0 5px 5px  rgba(4, 101, 4, 0.4);;
}

.test-card1:hover{
    transition:0.4s;
    transform:scale(1.1);
    box-shadow: 0 5px 5px  rgba(125, 123, 218, 0.4);
}

.test-card2:hover{
    transition:0.4s;
    transform:scale(1.1);
    box-shadow: 0 5px 5px  #ebe373;
}

